import React, { Component } from 'react';
import { requestHeaderGETWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, reloadPage } from '../../Utils/Utils';
import { Button } from 'reactstrap';
import { Loading } from '../Statuses/Statuses';
import Plot from 'react-plotly.js';
import { CButton } from '@coreui/react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { BACKEND_WEIGHT_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

const FILE_NAME = "WeightGraph.js";

const COLORS = [
    "red","green","blue","#b59a14","#b514a5","black","orange"
  ];

const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    weightListData: null,
    graphType: "scatter",
    graphMode: "lines+markers",
    dataForPlot_weight : null
};

class WeightList extends Component {

    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    componentDidMount() {
        document.title = "Satanlabs - weight graph page";
        this.fetchWeightDataList();
    }

    fetchWeightDataList(offset) {
        this.setState({
            isLoaded: false
        });
        let API_URL = BACKEND_WEIGHT_SERVICE_BASE_URL + '/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        weightListData: result.data
                    });
                    this.plotDataForWeight();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    plotDataForWeight = () => {
        this.setState({
            isLoaded: false
        });

        setTimeout(() => {
            this.setState({
                dataForPlot_weight: this.cleanDataForPlotting("weight"),
                isLoaded: true
            });
        }, 200);
        
    }

    cleanDataForPlotting = (type_of_data) => {
        let plotDatas = [];
    
        const randomColor = Math.floor(Math.random() * COLORS.length);
    
        let x = [] , y = [];
        this.state.weightListData.forEach(data => {
            x.push(data.createdOn);
            y.push(data.value);
        });
    
        plotDatas.push(
        {
            x: [ ...x ],
            y: [ ...y ],
            type:this.state.graphType,
            mode: this.state.graphMode,
            marker: {color: COLORS[randomColor]},
            name: `${type_of_data}`
        }
        );
    
        return plotDatas;
      }

    reloadData() {
        this.resetStateToDefault();
        this.fetchWeightDataList();
    }

    render() {
        Logger(FILE_NAME, "render", "", "this.state", this.state);
        const width = window.innerWidth * 0.85;
        const height = window.innerHeight * 0.60;

        return (
            <div>
            {
                <div style={{textAlign:"right"}}>
                <select onChange={e=>{
                    this.fetchWeightDataList(e.target.value);
                }}>
                    <option value="">Select no of items</option>
                    <option value={0} key={"all__a"}>All items</option>
                    <option value={10} key={"10__a"}>10</option>
                    <option value={100} key={"100__a"}>100</option>
                    <option value={1000} key={"1000__a"}>1000</option>
                    <option value={10000} key={"10000__a"}>10000</option>
                    <option value={100000} key={"100000__a"}>10000</option>
                    <option value={1000000} key={"1000000__a"}>1000000</option>
                </select>
                </div>
            }
            {
                this.state.isLoaded ? 
                <div>
                    <h2>Weight data list : </h2>
                    <CButton color="success" className="px-4" onClick={() => this.reloadData()}>reloadData</CButton>
                    <br /><br />
                    {<div>
                        <span>
                            <b>Change Type :</b>&nbsp;&nbsp;
                            <Button color="primary" size="lg" onClick={()=>{this.setState({graphType: "scatter"}); this.plotDataForWeight(); }}>scatter TYPE</Button>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <Button color="warning" size="lg" onClick={()=>{this.setState({graphType: "bar"}); this.plotDataForWeight(); }}>bar TYPE</Button>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            CURRENT Type : [ { this.state.graphType } ]
                            <br /><br />
                        </span>
                        <span>
                            <b>Change Mode :</b>&nbsp;&nbsp;
                            <Button color="primary" size="lg" onClick={()=>{this.setState({graphMode: "lines"}); this.plotDataForWeight(); }}>lines MODE</Button>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <Button color="danger" size="lg" onClick={()=>{this.setState({graphMode: "lines+markers"}); this.plotDataForWeight(); }}>lines+markers MODE</Button>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <Button color="warning" size="lg" onClick={()=>{this.setState({graphMode: "markers"}); this.plotDataForWeight(); }}>markers MODE</Button>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            CURRENT Mode : [ { this.state.graphMode } ]
                            <br /><br />
                        </span>
                    </div>
                    }
                    <Plot
                        showlegend={false}
                        data={ this.state.dataForPlot_weight }
                        layout={ {width, height, title: 'Weight data' } }
                    />
                    <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                    <FailureModal visible={this.state.failureModalVisible} />
                </div> 
                : <Loading />
            }
        </div>
    );
}

}

export default WeightList;
